import Vue from 'vue';
import Router from 'vue-router';
import Blog from '@/components/Blog';
import BlogPost from '@/components/BlogPost';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Blog',
      component: Blog,
    },{
      path: '/article/:id',
      name: 'Article',
      component: BlogPost,
    },{
      path: '/tag/:tag',
      name: 'Tag',
      component: Blog,
    },
  ],
});



